import './profileCard.css'

export const createProfileCard = () => {
    const profileCard = document.createElement('div')
    profileCard.classList = "profilecard"

    const profileCardChild = document.createElement('div')
    profileCardChild.classList = 'content-one'
    profileCardChild.textContent = "AH Bek"

    

    profileCard.appendChild(profileCardChild)

    return profileCard

}